}
	/* azul */
	/*color: #05386b; */
#logo:hover {
	text-shadow: 1px 2px 1px rgba(0,0,0,.1);
}
header ul {
	margin: 0;
	display: flex;
	height: 100%;
}
header ul li {
	padding: .5em;
	cursor: pointer;
/*	color: rgba(5,56,107,.8);*/
	color: #fef7f3;
	-webkit-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
