.task {
	padding: .5rem;
	color: #555;
	font-weight: 500;
	display: block;
	letter-spacing: .35pt;
	white-space: pre-line;
    word-break: break-word;
    padding: 15px 42.5px 15px 15px;
	line-height: 1.475;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
	position: relative;
	cursor: -webkit-grab;
	cursor: grab;
}
.task:active {
	cursor: -webkit-grabbing;
	cursor: grabbing;
}
.task p {
	display: inline-block;
}
.task span { vertical-align: top; height: 100%; padding: .5em 2.5px;}
.task span[contenteditable="true"]{
	border: none;
	opacity: .8;
	outline: none;
}
.task .close-button {
	font-size: 1.1rem;
	cursor: pointer;
	display: inline-block;
	position: absolute;
	bottom: 5px;
	right: 0;  
}
.task p:hover {text-decoration: line-through;}
